*{
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  background-color: gainsboro;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 20px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 200px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  align-content: center;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.load{
  font-size: 1.4rem;
}